@import url('https://rsms.me/inter/inter.css');

* {
    font-family: 'Inter var', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #000000;
    color: #cbcbcb;
    box-sizing: border-box;
}

a {
    transition: all 0.3s ease 0s;
    font-size: 16px;
    text-decoration: none;
}

a:hover {
    color: #ededed;
}
/* ----------------------------- */

/* Navbar Style */

@media screen and (max-width: 900px) {
    .cta {
      display: none;
    }
    a {
        font-size: 12px;
    }
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5%;
    border-bottom: 0.1px solid #202020;
}

.nav-links {
    list-style: none;
}

.nav-links li {
    display: inline-block;
}

.nav a {
    padding: 9px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

/* ----------------------------- */

/* Header Style */


.header {
    text-align: center;
    height: 100%;
    width: 100%;
}

.header-text .discraption a {
    color: #828282;
}
.header-text .discraption a:hover {
    color: #ffffff;
} 

@media screen and (max-width: 900px) {
    .header-text .title {
        margin-top: 70px;
        font-size: 15px;
        font-weight: 300;
    }
    .header-text .discraption {
        margin: 20px;
        font-size: 13px;
    }
    .BgAnimation__svg {
        max-width: 500px;
    }
}

@media screen and (min-width: 900px) {
    .header-text .title {
        margin-top: 70px;
        font-size: 20px;
        font-weight: 300;
    }

    .header-text .discraption {
        margin-top: 20px;
    }
    .content {
        max-width: 850px; 
        margin: 0 auto !important; 
        float: none !important; 
    }
    .BgAnimation__svg {
        margin-top: 50px;
    }
}
  

/* ----------------------------- */
/* Projects Style */

@media screen and (max-width: 900px) {
    .project-list ul li a {
        font-size: 10px;
    }
    .projects p {
        font-size: 30px;
    }
    .projects {
        margin: 20px;
    }
}

.projects p {
    font-size: 40px;
}
.project-list {
    width: 100%;
    height: 400px;
    margin-top: 30px;
    margin-bottom: 30px;
}
.project-list ul {
    list-style: none;
}
.project-list ul li {
    margin-top: 30px;
}
.project-list ul li a {
    font-size: 20px;
}

.projects .searchbar {
    margin-top: 50px;
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:300px;
    border:none;
    border-bottom:1px solid #2d2d2d;
    outline:none; 
}